import React, { useState } from "react";
import { Link } from "react-router-dom";
import ChatBot from "../ChatBot/ChatBot";
import BotButton from "../../component/BotButton/BotButton";
import './Consultation.css'

function Consultation() {
  const user = 1;
  const Specialisation=[
    {'name':'NEUROLOGIST', 
    'image':"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR7c2_AMGyC50nnvnnhJxO7aeLcKybOoGDQNg&usqp=CAU"},
    {
      'name':'PHYSICIAN',
      'image':'data:image/jpeg;base64,'
    },
    {
      'name':'DENTIST',
      'image':'data:image/jpeg;base64,'
    },
    {
      'name':'ENT SPECIALIST',
      'image':'data:image/jpeg;base64,'
    },
    {
      'name':'ORTHOPEDIST',
      'image':'https://media.istockphoto.com/photos/male-doctor-hold-in-arm-silver-pen-and-look-at-xray-picture-id1069111954?b=1&k=20&m=1069111954&s=170667a&w=0&h=R_8zIPaXeXTj1qATGY-qdxAmCyNqyLraA8yKlVUBalI='
    }
  ]  
  const [search, setSearch] = useState("");
  
  const searchDetect = (event) => {
    setSearch(event.target.value);
  };

  const checkSearch = (ele)=>{
    return ele.name.includes(search.toUpperCase())
  }

  return (
    <div className="container">
      {user != null ? (
        <div>
          <Link to='/consult/bot'>
              <BotButton />
          </Link>
          <div className="input-group my-5 ">
            <input
              type="text"
              className="form-control"
              placeholder="Specialisation"
              onChange={searchDetect}
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
            />
            <button
              className="btn btn-outline-secondary"
              type="button"
              id="button-addon2"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-search"
                viewBox="0 0 16 16"
              >
                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
              </svg>
            </button>
            <span className="fs-4 mx-4">Search</span>
          </div>
          <div className="row">
          {Specialisation.filter(checkSearch).map((special,index)=>{
            const navigation=`/consult/${special.name}`
            return(
              <div key={index} className="col-md-3 col-sm-6 p-3" >
              <Link to={navigation} style={{textDecoration: 'none'}}>
              
                <div className="card shadow text-dark ">
                  <img
                    height="150px"
                    width="150px"
                    src={special.image}
                    className="card-img-top"
                    alt="..."
                  />
                  <div className="card-body text-center ">
                    <h5 className="card-title">
                      {special.name.toUpperCase()}
                    </h5>
                  </div>
                </div>
              </Link>
            </div>

            )
          })}
          </div>
        </div>
      ) : (
        <div>
            <ChatBot />
        </div>
      )}
    </div>
  );
}

export default Consultation;
